
@import url('https://fonts.googleapis.com/css?family=Monoton|Rubik:400,700|Cabin');
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700|Roboto+Slab:400,700|Material+Icons');

body, html {
    font-family: 'Cabin', sans-serif;
    background: -webkit-gradient(linear, left top, right top, from(rgb(255, 255, 255)), to(rgb(255, 255, 184)));
    background: -webkit-linear-gradient(left, rgb(255, 255, 255), rgb(255, 255, 184));
    background: -o-linear-gradient(left, rgb(255, 255, 255), rgb(255, 255, 184));
    background: linear-gradient(to right, rgb(255, 255, 255), rgb(255, 255, 184));

}


